import { Box, Grid, Typography } from '@material-ui/core';
import React from 'react'
import Stepper from '../../../components/Stepper'
import { svgImages } from '../../../components/icons';
    function getSteps() {
        return ['Cargar masiva de productos?', 'Como cargar productos?', 'Como actualizar productos?', 'Como funcionan las columnas?'];
    }
    function getStepContent(step) {
        switch (step) {
            case 0:
                return(
                    <Grid container >
                        <Grid item xs={6} >
                            <Typography align='justify' >
                                <Box component="span" >
                                    La forma ms rpida de cargar una gran cantidad de productos y/o hacer modificaciones de productos ya existentes es a travs de un archivo <b>EXCEL (XLS)</b>,
                                    <b>para cargar archivos ser necesaria una plantilla</b> que nosotros le proporcionaremos para este fin, <b>la primera fila que contiene los ttulos de las columnas no 
                                    deben sufrir ningn tipo de alteracin </b> ni tampoco las validaciones de las mismas, <b>esta plantilla es nica para con el fin de cargar informacin al sistema, 
                                    </b> cualquier alteracin de la plantilla que no sea la de suministrar la informacin requerida puede llevar a generar errores en la carga de los productos. <br />
                                </Box>
                            </Typography>
                        </Grid>
                        <Grid item xs={6} >
                            <Box p={5} display="flex" justifyContent="center" alignItems="center" >
                                <img style={{ width: "80%" }} src={svgImages.mExcel} alt="Excel" />
                            </Box>
                        </Grid>
                    </Grid>
                )
            case 1:
                return(
                    <Grid container >
                        <Grid item xs={6} >
                            <Typography align='justify' >
                                <Box component="span" >
                                    Para comenzar a subir sus productos de forma "masiva" debe desde cargar la plantilla AQU, una vez tenga descargada y abierta la plantilla
                                    en su ordenador deber suministrar la informacin de los productos en el orden que aparece all. <b>Recuerde que la plantilla no debe sufrir 
                                    alteraciones ajenas a las de aadir la informacin requerida, </b> una vez haya terminado de cargar la informacin de sus productos, puede 
                                    proceder a subir la plantilla a la aplicacin a travs del botn Cargar archivo. La plantilla pasar por un proceso de validacin donde se le 
                                    sern informados a travs de notificaciones de los errores encontrados, al haber pasado la validacin ya estar listo para Enviar la informacin.
                                </Box>
                            </Typography>
                        </Grid>
                        <Grid item xs={6} >
                            <Box p={5} display="flex" justifyContent="center" alignItems="center" >
                                <img style={{ width: "80%" }} src={svgImages.mExcel} alt="Excel" />
                            </Box>
                        </Grid>
                    </Grid>
                )
            case 2:
                return(
                    <Grid container >
                        <Grid item xs={6} >
                            <Typography align='justify' >
                                <Box component="span" >
                                    De vez en cuando necesitar actualizar la informacin de productos ya registrados, para esto recomendamos utilizar los campos Cdigo de barras o Cdigo SKU como identificadores unicos, ya que estos son irrepetibles y la aplicacin no admite cdigos duplicados, haciendo uso de esto podemos usarlo para actualizar algn producto ya existente, solamente deberemos aadir la informacin del producto en la plantilla junto con sus o su cdigo para que este sea reconocido y actualizado, Teniendo en cuenta esto; debe tener precausion, ya que si enva un cdigo errado y existente el producto que tiene asignado dicho cdigo sufrir los cambios por eso recomendamos leer bien los cambios antes de subir los datos.
                                </Box>
                            </Typography>
                        </Grid>
                        <Grid item xs={6} >
                            <Box p={5} display="flex" justifyContent="center" alignItems="center" >
                                <img style={{ width: "80%" }} src={svgImages.mExcel} alt="Excel" />
                            </Box>
                        </Grid>
                    </Grid>
                )
            case 3:
                return(
                    <Grid container >
                        <Grid item xs={6} >
                            <Typography align='justify' >
                                <Box component="span" >
                                    De vez en cuando necesitar actualizar la informacin de productos ya registrados, para esto recomendamos utilizar los campos Cdigo de barras o Cdigo SKU como identificadores unicos, ya que estos son irrepetibles y la aplicacin no admite cdigos duplicados, haciendo uso de esto podemos usarlo para actualizar algn producto ya existente, solamente deberemos aadir la informacin del producto en la plantilla junto con sus o su cdigo para que este sea reconocido y actualizado, Teniendo en cuenta esto; debe tener precausion, ya que si enva un cdigo errado y existente el producto que tiene asignado dicho cdigo sufrir los cambios por eso recomendamos leer bien los cambios antes de subir los datos.
                                </Box>
                            </Typography>
                        </Grid>
                        <Grid item xs={6} >
                            <Box p={5} display="flex" justifyContent="center" alignItems="center" >
                                <img style={{ width: "80%" }} src={svgImages.mExcel} alt="Excel" />
                            </Box>
                        </Grid>
                    </Grid>
                )
            default:
                return 'Unknown step';
        }
    }

const Instructions = () => {
    return(
        <>
            <Stepper getSteps={getSteps} getStepContent={getStepContent} />
        </>
    )
}

export default Instructions